import { Injectable } from "@angular/core";
import {
  Http,
  URLSearchParams,
  Headers,
  RequestOptions,
  RequestMethod
} from "@angular/http";
import 'rxjs/add/operator/map'
/*
  Generated class for the FreshBooksApiProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FreshBooksApiProvider {
  constructor(public http: Http) {
    console.log("Hello FreshBooksApiProvider Provider");
  }

  getInvoices = () => {
    return new Promise(resolve => {
      /*
      curl -X GET 
      -H 'Authorization: Bearer 8763331008cf21cdf7a6ad3a36753734e599ff96d4b80544446da4033191dd00' 
      -H 'Api-Version: alpha' 
      -H 'Content-Type: application/json' 
      https://api.freshbooks.com/accounting/account/K5Vxa/invoices/invoices?search%5Bnotes%5D=Wednesday
      */
      let url =
        "/invoices/invoices?search%5Bnotes%5D=Wednesday";
      let headers = new Headers();
      headers.append("Content-Type", "application/json");
      headers.append("Api-Version", "alpha");
      headers.append(
        "Authorization",
        "Bearer 8763331008cf21cdf7a6ad3a36753734e599ff96d4b80544446da4033191dd00"
      );
      let options = new RequestOptions({ headers: headers });

      this.http
        .request(url, options)
        .map(res => res.json())
        .subscribe(data => {
          // we've got back the raw data, now generate the core schedule data
          // and save the data for later reference
          debugger;
          resolve();
        });
    });
  };
}



